'use client'

import Link from 'next/link'

export default function Home() {
  return (
    <main className="min-h-screen flex flex-col">
      <header className="bg-gradient-to-r from-[#0057A0] to-[#C8102E] text-white px-8 py-6 flex justify-between items-center">
        <h1 className="text-2xl font-bold">Prime Pillars</h1>
        <nav>
          <ul className="flex gap-6 font-semibold">
            <li><Link href="#">FAQs</Link></li>
            <li><Link href="#">Downloadable Forms</Link></li>
            <li><Link href="#">Contact Us</Link></li>
          </ul>
        </nav>
      </header>

      <section className="flex flex-1 justify-center items-center bg-[#f0f2f5] px-4">
        <div className="bg-white p-8 rounded-lg shadow-md max-w-xl w-full flex gap-8">
          <div className="w-2/5 flex justify-center items-center">
            <img src="https://via.placeholder.com/100x100.png?text=Logo" alt="Logo" className="max-w-[100px]" />
          </div>
          <div className="w-3/5">
            <h2 className="text-2xl font-bold text-[#0057A0] mb-4">Login</h2>
            <form className="space-y-4">
              <div>
                <label htmlFor="pen" className="block font-semibold mb-1">PEN Number</label>
                <input type="text" id="pen" name="pen" className="w-full px-3 py-2 border rounded-md border-gray-300" />
              </div>
              <div>
                <label htmlFor="password" className="block font-semibold mb-1">Password</label>
                <input type="password" id="password" name="password" className="w-full px-3 py-2 border rounded-md border-gray-300" />
              </div>
              <button type="submit" className="w-full bg-[#0057A0] text-white font-semibold py-2 rounded hover:bg-[#003f7a]">Login</button>
              <button type="button" className="w-full bg-gray-200 text-gray-800 font-semibold py-2 rounded hover:bg-gray-300">Register</button>
            </form>
          </div>
        </div>
      </section>

      <footer className="bg-gray-800 text-white text-center text-sm py-4">
        &copy; 2025 Prime Pillars Real Estate Development Company. All rights reserved.
      </footer>
    </main>
  )
}
